@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
.layout_app__29yFf {
  max-width: 100%;
  margin: 0 auto;
  font-family: Lato;
  /* font-size: 20px; */
}
header {
  background: rgb(211, 174, 10);
  background: linear-gradient(0deg, rgba(211, 174, 10, 1) 0%, rgba(211, 174, 10, 1) 35%, rgb(102, 89, 33) 100%);
}

header ul {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 75%;
  margin: auto;
}

header ul span {
  color: white;
  padding: 0.5em;
}

.header_mini-navbar__3rdul ul {
  border: 1px solid #bd9922;
  border-radius: 0.3em;
  padding-left: 0;
  width: 100%;
}

header li {
  list-style: none;
  margin: .35em;
  padding: 0 .95em;
}

header a {
  text-decoration: none;
  letter-spacing: 2px;
  color: white;
  cursor: pointer;
}

header a:hover {
  color: black;
}

nav {
  border-top: 1px solid #bd9922;
}

.header_logo__S7V73 {
  font-size: 1.5em;
  color: rgb(255, 255, 255);
  padding-bottom: 0;
}

.header_logo-wrap__2bSI9 {
  display: flex;
  font-family: 'Monofett', cursive;
  letter-spacing: .5em;
  margin: 0.5em 0;
}

.header_mini-navbar-wrap__3xjQ0 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 75%;
  margin: auto;
}

